@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300;400;500;600;700;800&family=Open+Sans:wght@300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,600;0,700;1,600;1,700&family=Open+Sans:wght@300;400;500;700&display=swap");
*, *::after, *::before {
  padding: 0px;
  margin: 0px;
  position: relative;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  position: relative;
  overflow: hidden;
  font-family: "Noto Sans", sans-serif;
  color: #fff;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
  overflow: hidden !important;
  background-color: #393b44;
  background: none;
}

section.mobileContainer {
  opacity: 0;
  z-index: 9999;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px 20px;
  padding-top: 0px;
  overflow: hidden;
  min-width: 320px;
  min-height: 550px;
  height: 550px;
  border-radius: 25px;
  background: #000;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60px auto;
}

.header {
  z-index: 999;
  position: sticky;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 70px;
  background-color: #000;
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
}
.header::after, .header::before {
  z-index: 999;
  content: "";
  position: absolute;
  top: 0px;
  height: 100%;
  width: 100%;
  background: inherit;
}
.header::after {
  left: -100%;
}
.header::before {
  right: -100%;
}
.header .iconsCon {
  width: 100%;
  height: 35px;
  display: grid;
  grid-template-columns: 1fr auto auto auto;
  grid-template-rows: 1fr;
  align-items: center;
  grid-column-gap: 12px;
  font-size: 14px;
}
.header .iconsCon .battery {
  font-size: 20px;
}
.header .navBtns {
  width: 100%;
}
.header .navBtns button {
  height: 100%;
  height: 30px;
  width: 30px;
  font-size: 20px;
  color: #fff;
  background: none;
  cursor: pointer;
  padding: 0px;
  text-align: start;
  outline: none;
  border: none;
}

.Mblbody {
  grid-area: 2/1/3/2;
}

.topWrapper {
  height: 120px;
}
.topWrapper .title_profile_con {
  height: 80px;
  display: grid;
  grid-template-columns: 1fr auto;
  justify-content: space-between;
  align-items: center;
}
.topWrapper .title_profile_con h2 {
  padding: 0px;
  font-size: 26px;
  font-weight: 700;
}
.topWrapper .title_profile_con .profileImage {
  margin-bottom: 6px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: grid;
  justify-items: center;
  align-items: center;
  color: #000;
  background: #ddd;
  font-size: 16px;
}
.topWrapper .AmountDisplay {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 4px;
}
.topWrapper .AmountDisplay h6 {
  color: #ddd;
  font-weight: 400;
  font-size: 12px;
}
.topWrapper .phaseSecondTop {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  display: grid;
  justify-content: center;
  align-content: center;
}
.topWrapper .phaseSecondTop h4 {
  opacity: 0;
  display: grid;
  justify-items: center;
  font-size: 18px;
  font-weight: 400;
}
.topWrapper .phaseSecondTop p {
  opacity: 0;
  font-weight: 300;
  font-size: 14px;
}

.cardsContainerWrapper {
  position: relative;
  margin-top: 15px;
  width: 100%;
  height: 300px;
}
.cardsContainerWrapper .cardsMainContainerScroll {
  overflow-y: hidden;
  overflow: hidden !important;
  position: absolute;
  z-index: 999999;
  bottom: 0px;
  left: -20px;
  width: calc(100% + 40px);
  height: 100%;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(8, auto);
  grid-template-rows: 1fr;
  padding-bottom: -20px;
  align-items: end;
}

.card {
  position: relative;
  overflow: hidden;
  margin-left: 20px;
  display: inline-block;
  height: 300px;
  width: 200px;
  border-radius: 10px;
  cursor: pointer;
  background-size: cover;
}
.card:nth-child(4) {
  border: none !important;
}

ul.cul {
  padding: 20px;
  border-radius: inherit;
  z-index: 99999;
  top: 50px;
  left: -50px;
  transform-origin: center center;
  transform: rotate(-90deg);
  overflow: hidden;
  position: absolute;
  height: 200px;
  width: 300px;
  padding: 20px;
  display: grid;
  grid-template-columns: 10px 1fr 100px;
  grid-template-rows: repeat(3, 1fr);
}
ul.cul li {
  list-style-type: none;
  display: grid;
  align-items: center;
  color: rgb(237, 228, 228);
}
ul.cul .c_heading {
  grid-area: 1/1/2/3;
  font-size: 20px;
  font-weight: 600;
}
ul.cul .c_Icon_top {
  grid-area: 1/3/2/4;
  height: 35px;
  width: 35px;
  align-self: center;
  justify-self: end;
}
ul.cul .c_Icon_top img {
  width: 100%;
  height: 100%;
  transform: rotate(90deg);
}
ul.cul .c_chip {
  grid-area: 2/1/3/3;
  height: 45px;
  width: 48px;
  display: grid;
  justify-items: center;
  align-items: end;
}
ul.cul .c_chip svg {
  width: 100% !important;
  height: 100% !important;
}
ul.cul .c_number {
  grid-area: 2/2/3/4;
  display: grid;
  align-items: end;
  justify-items: end;
  height: 45px;
  font-size: 18px;
  font-weight: 600;
}
ul.cul .card_holder {
  align-self: end;
  grid-area: 3/1/4/3;
  display: grid;
}
ul.cul .card_holder p {
  font-size: 10px;
  font-weight: 400;
  text-transform: uppercase;
}
ul.cul .card_holder h4 {
  font-size: 14px;
  font-weight: 600;
}
ul.cul .VisaLogo {
  grid-area: 3/3/4/4;
  justify-self: end;
  align-self: end;
  width: 100%;
  height: 35px;
  display: grid;
  align-items: end;
  justify-items: end;
}
ul.cul .VisaLogo svg {
  height: 100%;
  width: auto;
}
ul.cul .VisaLogo h2.visaText {
  display: grid;
  justify-self: end;
  align-items: end;
  justify-items: end;
  width: 100%;
  height: 100%;
  font-size: 30px;
  font-weight: 900;
  width: 100%;
  padding: 0px;
  font-family: "Noto Sans", sans-serif;
}

.mobileContainer::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.mobileContainer {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.mobileContainer {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.cardsMainContainerScroll::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.cardsMainContainerScroll {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.cardsMainContainerScroll {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}

