.newCardBgFix {
  z-index: 7;
  position: absolute;
  height: 100%;
  width: 200%;
  left: -50%;
  top: 0px;
  background: #000;
}

.cardsContainerWrapper {
  perspective: 800px !important;
}

.newCard {
  z-index: 8;
  position: absolute;
  left: 0%;
  bottom: 0%;
  height: 300px;
  width: 200px;
  border-radius: 10px;
  transform-style: preserve-3d !important;
  cursor: pointer;
}
.newCard .newCUL {
  backface-visibility: hidden !important;
}
.newCard .backFace {
  backface-visibility: hidden !important;
  position: absolute;
  height: 100%;
  width: 100%;
  transform-origin: center center;
  transform: rotateX(180deg);
  color: #fff;
  overflow: hidden;
  border-radius: 10px;
}
.newCard .backFace .BFCover {
  position: absolute;
  height: 150px;
  width: 145%;
  left: 50%;
  top: 50%;
  transform-origin: center center;
  transform: translate(-50%, -50%) rotate(-90deg);
  display: grid;
  justify-items: start;
  align-items: end;
  padding-bottom: 30px;
}
.newCard .backFace .BFCover .BFPin {
  width: 100%;
  padding: 5px 10px;
  border-radius: 2px;
  background: rgb(255, 254, 254);
  font-weight: 600;
}
.newCard .backFace .BFCover .BFLine {
  position: absolute;
  bottom: 62px;
  left: -50%;
  width: 200%;
  height: 30px;
}

.CButtonsCon {
  width: 52px;
  height: 50px;
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-template-rows: 1fr;
  overflow: hidden;
  justify-content: space-between;
  align-items: center;
}
.CButtonsCon .ctbn {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: rgb(126, 124, 124);
}

ul.listSectionFirst {
  background: rgb(50, 48, 48);
  list-style-type: none;
  width: 100%;
  margin: 0px;
  min-height: 460px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-auto-rows: auto;
  align-content: start;
}
ul.listSectionFirst::after, ul.listSectionFirst::before {
  content: " ";
  position: absolute;
  top: 0px;
  height: 100%;
  width: 100%;
  background: inherit;
}
ul.listSectionFirst::after {
  left: -100%;
}
ul.listSectionFirst::before {
  right: -100%;
}
ul.listSectionFirst li {
  position: relative;
  border-bottom: 1px solid rgb(64, 62, 62);
  padding: 10px 0px;
  height: auto;
  width: 100%;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: 1fr;
  grid-column-gap: 20px;
}
ul.listSectionFirst li .li_logo {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  display: grid;
  justify-items: center;
  align-items: center;
}
ul.listSectionFirst li .li_logo .svgContainer {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  display: grid;
  justify-items: center;
  align-items: center;
}
ul.listSectionFirst li .li_logo .svgContainer p {
  font-weight: 600;
}
ul.listSectionFirst li .li_logo .appleLogo {
  height: 20px !important;
  width: 20px !important;
}
ul.listSectionFirst li .li__mid h2.li_Title {
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
}
ul.listSectionFirst li .li__mid p.li_description {
  font-size: 12px;
  font-weight: 300;
  line-height: 1.2;
}
ul.listSectionFirst li h2.li_Amount {
  font-size: 14px;
  font-weight: 500;
  align-self: end;
}
ul.listSectionFirst li::after, ul.listSectionFirst li::before {
  top: 100%;
  content: " ";
  position: absolute;
  width: 100%;
  height: 1px;
  background: rgb(64, 62, 62);
}
ul.listSectionFirst li::after {
  z-index: 999;
  left: -100%;
}
ul.listSectionFirst li::before {
  right: -100%;
}
ul.listSectionFirst li.listHeading {
  padding-top: 15px;
  position: relative;
  display: block;
  font-size: 18px;
  font-weight: 600;
}
ul.listSectionFirst li.listHeading .ulFix {
  background: #000;
  z-index: 9;
  top: -2px;
  position: absolute;
  height: 15px;
  width: calc(100% + 40px);
  left: 50%;
  transform: translateX(-50%);
}
ul.listSectionFirst li.listHeading .ulFix::after {
  content: " ";
  position: absolute;
  background: rgb(50, 48, 48);
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

/*** LOADER ***/
.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}

.lastLoader {
  width: 100%;
  height: 100%;
  background: none;
  border-radius: none !important;
}